}
.footer, .footer a {
    color: #6E717C
}
.footer .license {
    float: right
}
.footer .license img {
    vertical-align: -5px;
    margin-left: 4px
}
.btn {
    display: inline-block;
    height: 38px;
    width: 120px;
    border: 1px solid #fff;
    border-radius: 19px;
    color: #fff;
    line-height: 38px;
    letter-spacing: 0.5px;
    font-weight: 400
}
.btn-primary {
    background: #006FE5;
    border-color: #006FE5
}
.btn-primary:hover {
    box-shadow: 0px 4px 8px 0px #0E3D6F;
    color: #fff;
    -webkit-transition: box-shadow 0.3s;
    transition: box-shadow 0.3s
}
.index {
    text-align: center
}
.index-bg-dark {
    background: #1c9adc;
    color: #dbdde6
}
.index-bg-light {
    background: #FFFFFF
}
.block {
    padding: 100px 0
}
.banner h1 {
    font-size: 32px;
    font-weight: 200;
    letter-spacing: 1px;
    margin-bottom: 20px
}
.banner .banner-logo {
    height: 280px
}
.banner .banner-button {
    padding-top: 50px
}
.version {
    background: #F6F8F8;
    height: 60px;
    line-height: 60px;
    box-shadow: 0px 1px 0px 0px rgba(225, 225, 225, 0.5)
}
.version span {
    padding: 0 30px;
    color: #71747B
}
.version strong {
    color: #131926;
    font-weight: 700
}
.info h3 {
    font-weight: 500;
    color: #333333;
    font-size: 24px;
    margin-bottom: 20px
}
.info ul {}
.info li {
    vertical-align: top;
    height: 380px;
    font-size: 16px
}
.info .info-img {
    float: left;
    padding-left: 149px;
    padding-top: 133px
}
.info .info-write {
    height: 100px;
    float: left;
    padding-left: 149px;
    padding-top: 133px
}
.info .info-bg-grey {
    background-color: #f4f5f9
}
.guide-code {
    font-size: 20px;
    font-family: Consolas, "Liberation Mono", Menlo, Courier, monospace;
    font-weight: 200;
    color: #FFFFFF;
    text-align: left;
    padding: 40px 0
}
.guide-code ul {
    background: #1F2532;
    max-width: 650px;
    margin: 0 auto;
    padding: 40px
}
.guide-code li {
    list-style: none;
    line-height: 60px
}
.guide-code strong {
    color: #d4d4d4;
    margin-right: 30px;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none
}
.page-title {
    padding: 40px 0 160px;
    background: #1c9adc;
    text-align: center
}
.page-title h1 {
    text-shadow: 3px 4px 0px rgba(255, 255, 255, 0.1);
    letter-spacing: 2px;
    font-size: 64px;
    color: #fff;
    font-weight: 500
}
.page-title h2 {
    font-size: 16px;
    color: #4E5668;
    padding-top: 5px;
    font-weight: 300;
    letter-spacing: 0.5px
}
.page-main {
    margin: -100px auto 50px;
    max-width: 1004px;
    min-height: 200px;
    overflow: hidden;
    background: #FFFFFF;
    box-shadow: 0px 1px 1px 0px #E3E3E3;
    border-radius: 2px
}
.page-main article {
    width: 760px;
    float: right;
    min-height: 400px;
    padding: 20px;
    border-left: 1px solid #EFEFEF
}
.mobile-menu {
    display: none
}
.mobile-menu ul {
    padding-left: 20px;
    margin-bottom: 40px
}
.mobile-menu ul li a {
    color: #000000
}
@media screen and (max-width: 1004px) {
    .nav, footer {
        padding: 0 20px
    }
    .nav ul {
        display: none
    }
    .nav header {
        text-align: center
    }
    .mobile-menu {
        display: block
    }
    .page-main aside {
        display: block;
        position: absolute;
        background: #fff;
        top: 0px;
        left: -210px;
        width: 200px;
        border-right: 1px solid #eee;
        box-shadow: 0px 0px 4px 0px #ddd;
        -webkit-transition: left 1s;
        transition: left 1s
    }
    .mobile-trigger {
        display: inline-block;
        top: 15px;
        right: -50px;
        -webkit-transition: right 1s;
        transition: right 1s;
        position: absolute
    }
    .mobile-trigger li {
        background: #fff;
        width: 30px;
        height: 6px;
        margin-bottom: 4px
    }
    .page-main aside.mobile-show {
        left: 0;
        -webkit-transition: left 1s;
        transition: left 1s
    }
    .page-main aside.mobile-show .mobile-trigger {
        right: 15px;
        -webkit-transition: right 1s;
        transition: right 1s
    }
    .page-main aside.mobile-show .mobile-trigger li {
        background: #000
    }
    .page-main article {
        float: none;
        width: auto
    }
}
.release {
    padding: 40px
}
.release h1 {
    font-size: 1.5em
}
.page-home-show {
    margin: 0 auto;
    padding: 40px 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center
}




